.first-path--container {
  display: flex;
  justify-content: center;
  background-image: url("./img/forest1.svg");
  background-size: cover;
  height: 100vh;
}
.first-path--container .first-path--ul {
  margin: 0;
  width: 15%;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  list-style: none;
}
.first-path--container .first-path--ul .first-path--lower-arrows-wrapper,
.first-path--container .first-path--ul .first-path--upper-arrows-wrapper {
  display: flex;
  justify-content: space-around;
}
.first-path--container .first-path--ul .first-path--upper-arrows-wrapper {
  width: 40%;
  margin: 1rem 40%;
}
.first-path--container .first-path--ul .first-path--li-arrow-left {
  margin-right: 3rem;
}
.first-path--container .first-path--ul .first-path--li-diagonally-right {
  margin-top: 1.5rem;
  padding-left: 1rem;
}/*# sourceMappingURL=first-path.css.map */